import React, { Component, Fragment } from 'react';
import { Table, Select } from 'antd';

const { Option } = Select;
const dataSource = [
  {
    lop: 'SCOT1',
    mon: 'Anh vn 1',
    sohoctrinh: '6',
    lopghep: '',
    loaigio: '1',
    topthu: '0',
    stiecctdt: '60',
    stiectuan: '6',
    stieckh: '60',
    stiectkb: '12',
    ca: '0',
    giaidoan: '1',
    tutuan: '1',
    dentuan: '10',
    tungay: '09/08/2019',
    denngay: '14/10/2020',
  },
  {
    lop: 'SCOT2',
    mon: 'Gii tch 1',
    sohoctrinh: '2',
    lopghep: '',
    loaigio: '1',
    topthu: '0',
    stiecctdt: '30',
    stiectuan: '4',
    stieckh: '30',
    stiectkb: '8',
    ca: '0',
    giaidoan: '1',
    tutuan: '1',
    dentuan: '10',
    tungay: '09/08/2019',
    denngay: '14/10/2020',
  },
];

const columns = [
  {
    title: 'Tn lp',
    dataIndex: 'lop',
    key: 'lop',
  },
  {
    title: 'Tn mn',
    dataIndex: 'mon',
    key: 'mon',
  },
  {
    title: 'S hc trnh',
    dataIndex: 'sohoctrinh',
    key: 'sohoctrinh',
  },
  {
    title: 'Lp ghp',
    dataIndex: 'lopghep',
    key: 'lopghep',
  },
  {
    title: 'Loi gi',
    dataIndex: 'loaigio',
    key: 'loaigio',
  },
  {
    title: 'Tp th',
    dataIndex: 'topthu',
    key: 'Tp th',
  },
  {
    title: 'S.tit CTT',
    dataIndex: 'stiecctdt',
    key: 'stietctdt',
  },
  {
    title: 'S.tit/tun',
    dataIndex: 'stiectuan',
    key: 'stiettuan',
  },
  {
    title: 'S.tit xp KH',
    dataIndex: 'stieckh',
    key: 'stietkh',
  },
  {
    title: 'S.tit xp TKB',
    dataIndex: 'stiectkb',
    key: 'stiettkb',
  },
  {
    title: 'Ca',
    dataIndex: 'ca',
    key: 'ca',
  },
  {
    title: 'Giai on',
    dataIndex: 'giaidoan',
    key: 'giaidoan',
  },
  {
    title: 'T tun',
    dataIndex: 'tutuan',
    key: 'tutuan',
  },
  {
    title: 'n tun',
    dataIndex: 'dentuan',
    key: 'dentuan',
  },
  {
    title: 'T ngy',
    dataIndex: 'tungay',
    key: 'tungay',
  },
  {
    title: 'n ngy',
    dataIndex: 'denngay',
    key: 'denngay',
  },
  {
    title: 'Gio vin',
    key: 'giaovien',
    dataIndex: 'gvcn',
    render: () => (
      <Fragment>
        <Select defaultValue="lucy" style={{ width: '100%' }}>
          <Option value="jack">Trn Vn A</Option>
          <Option value="lucy">Nguyn Vn B</Option>
          <Option value="Yiminghe">Trn Vn C</Option>
        </Select>
      </Fragment>
    ),
  },
  {
    title: 'Phng',
    dataIndex: 'phong',
    key: 'phong',
    render: () => (
      <Fragment>
        <Select defaultValue="1" style={{ width: '100%' }}>
          <Option value="1">Phng 101</Option>
          <Option value="2">Phng 102</Option>
        </Select>
      </Fragment>
    ),
  },
  {
    title: 'Ghi ch',
    dataIndex: 'ghichu',
    key: 'ghichu',
  },
];

export default class TableClasses extends Component {
  state = {};
  render() {
    return (
      <Table scroll={{ x: true }} bordered size="small" dataSource={dataSource} columns={columns} />
    );
  }
}



