import React, { Component } from 'react';
import ReactDataSheet from 'react-datasheet';
// Be sure to include styles at some point, probably during your bootstrapping
import 'react-datasheet/lib/react-datasheet.css';
import { Col, Row, Button, Modal, message } from 'antd';
import { dataClasses } from 'dump/data';
import './styles.scss';
import FormSelectPlan from './FormSelectPlan';

const dataDemo = [];
for (let i = 0; i < 10; i += 1) {
  const item = [];
  for (let j = 0; j < 6; j += 1) {
    let rowspan = 1;
    if (i === 0 && j === 1) {
      rowspan = 4;
    }
    item.push({ value: '', code: 10 * i + j, rowspan });
  }
  dataDemo.push(item);
}

export default class DataSheet extends Component {
  state = {
    visibleSelectPlan: false,
    lessonTime: [
      {
        ca: 'Sng',
        lession: 1,
      },
      {
        ca: 'Sng',
        lession: 2,
      },
      {
        ca: 'Sng',
        lession: 3,
      },
      {
        ca: 'Sng',
        lession: 4,
      },
      {
        ca: 'Sng',
        lession: 5,
      },
      {
        ca: 'Chiu',
        lession: 5,
      },
      {
        ca: 'Chiu',
        lession: 6,
      },
      {
        ca: 'Chiu',
        lession: 7,
      },
      {
        ca: 'Chiu',
        lession: 8,
      },
      {
        ca: 'Chiu',
        lession: 9,
      },
      {
        ca: 'Chiu',
        lession: 10,
      },
    ],
    grid: JSON.parse(JSON.stringify(dataDemo)),
    codeSelected: [],
  };

  showModalSelectPlan = () => {
    if (document.getElementsByClassName('selected').length === 0) {
      message.warning('Vui lng chn thi gian');
      return;
    }
    const codeSelected = {};
    Array.from(document.getElementsByClassName('selected')).forEach(item => {
      codeSelected[item.dataset.code] = true;
    });
    console.log(codeSelected);
    this.setState({
      visibleSelectPlan: true,
      codeSelected,
    });
  };
  handleOkSelectPlan = () => {
    this.setState({
      visibleSelectPlan: false,
    });
  };

  handleSelectPlan = planCode => {
    const { grid, codeSelected } = this.state;
    const gridObjCodeSelect = {};
    grid.forEach(item => {
      item.forEach(item2 => {
        if (codeSelected[item2.code]) {
          item2.value = planCode;
          gridObjCodeSelect[item2.code] = item2;
        }
      });
    });
    const gridNew = JSON.parse(JSON.stringify(grid));
    this.setState({ grid: gridNew });
    this.handleOkSelectPlan();
  };

  handleCancelSelectPlan = e => {
    console.log(e);
    this.setState({
      visibleSelectPlan: false,
    });
  };

  handleDeletePlan = () => {
    if (document.getElementsByClassName('selected').length === 0) {
      message.warning('Vui lng chn thi gian');
      return;
    }
    const codeSelected = {};
    Array.from(document.getElementsByClassName('selected')).forEach(item => {
      codeSelected[item.dataset.code] = true;
    });
    const { grid } = this.state;
    const gridObjCodeSelect = {};
    grid.forEach(item => {
      item.forEach(item2 => {
        if (codeSelected[item2.code]) {
          item2.value = '';
          gridObjCodeSelect[item2.code] = item2;
        }
      });
    });
    const gridNew = JSON.parse(JSON.stringify(grid));
    this.setState({ grid: gridNew });
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <ReactDataSheet
            style={{ width: '100%' }}
            data={this.state.grid}
            onCellsChanged={changes => {
              console.log(changes);
              const grid = this.state.grid.map(row => [...row]);
              changes.forEach(({ row, col, value }) => {
                grid[row][col] = { ...grid[row][col], value };
              });
              this.setState({ grid });
            }}
            sheetRenderer={props => (
              <table
                style={{ width: '100%' }}
                className={`${props.className} my-awesome-extra-class`}
              >
                <thead>
                  <tr>
                    <th className="header-action" colSpan="8">
                      <Button onClick={this.handleDeletePlan}>Xa mn</Button>
                      <Button onClick={this.showModalSelectPlan}>Xp mn</Button>
                    </th>
                  </tr>
                  <tr>
                    <th>Ca hc</th>
                    <th>Tit</th>
                    <th>Th 2</th>
                    <th>Th 3</th>
                    <th>Th 4</th>
                    <th>Th 5</th>
                    <th>Th 6</th>
                    <th>Th 7</th>
                  </tr>
                </thead>
                <tbody>{props.children}</tbody>
              </table>
            )}
            rowRenderer={props => (
              <tr>
                <td>{this.state.lessonTime[props.row].ca}</td>
                <td>{this.state.lessonTime[props.row].lession}</td>
                {props.children}
              </tr>
            )}
            valueRenderer={cell => (
              <div
                style={{
                  background: !dataClasses[cell.value] ? '#fff' : dataClasses[cell.value].color,
                  height: 27,
                  textAlign: 'center',
                  verticalAlign: 'middle',
                  lineHeight: '27px',
                  // color: '#fff',
                }}
              >
                {cell.value}
              </div>
            )}
            attributesRenderer={cell => ({ 'data-code': cell.code } || {})}
          />
        </Col>
        <Modal title="La chn mn" visible={this.state.visibleSelectPlan} footer={null}>
          <FormSelectPlan
            handleSelectPlan={this.handleSelectPlan}
            onCancel={this.handleCancelSelectPlan}
          />
        </Modal>
      </Row>
    );
  }
}



